@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;1,200;1,400&display=swap);
@import url(https://cdn.jsdelivr.net/npm/@fortawesome/fontawesome-free@5.15.4/css/fontawesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{
    font-family: 'Source Sans Pro', sans-serif;
}

:root{
    --nav-color: hsl(210,11%,15%);
    --nav-color-hover: hsl(210,11%,45%);
    --nav-border-color:hsl(210,11%,45%);
    --light-color:hsl(209,100%,92%);
    --font-color: hsl(0,0%,100%);
    --font-color-hover: hsl(0,0%,40%);
}
.navigation_container{
    background-color: var(--nav-color);
    color:white
}

.navbar-light .navbar-nav .nav-link{
    color:var(--font-color) !important
}

.navbar-light .navbar-brand{
    color:var(--font-color) !important
}
.navbar-light .navbar-toggler-icon{
    -webkit-filter: invert(1);
            filter: invert(1);
}

.links a:hover{
 background-color: var(--font-color-hover);
}
.mainInfo{
    display: flex;
    position:absolute;
    color:var(--font-color);
    top:0;
    left:0;
    right:0;
    bottom:0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
}
.about__container{
 margin: 3rem 0;
 display:flex;
 flex-direction: row;
 justify-content:space-evenly;
 align-items: center;
 position: absolute;
 z-index: 1;
 width:100% ;
 padding-top: 5rem;
}
.about__container img{
    border-radius: 50%;
    border:0.5px solid rgb(218, 218,218);
    margin-right: '3rem'!important;
    /* display: flex;
    direction: row;
    justify-content: space-evenly */
    max-width:500px;
    max-height:500px;
    width:100% ;
    object-fit: scale-down;
}

.about__text{
    max-width:600px;
    z-index: 1;
    color:var(--font-color);
    

}
.about__container #image{
   margin-top:'4rem';
   /* width:100% ;
  margin-right: auto */
}

@media only screen and (max-width: 900px) {
    .about__container{
        flex-direction: column;
    }
    
}

@media only screen and (max-width: 700px) {
    .about__text{
        max-width: 300px;
    }
    
}

.technologies__container {
    /* background-color: var(--light-color); */
    color: var(--nav-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5rem 3rem 3rem 3rem;
    margin-top: 3rem;
    position: absolute;
    z-index: 1;
    height: 100vh;
    background-color:rgba(0, 0, 0, 0);
    width:100%;

  }
  .technologies__container h1{
    margin-top: '2rem';
  }
  
  .tech__cards__container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1100px;
    margin: 1rem 0;
  }
  
  .tech__card {
    width: 15rem;
    height: 10rem;
    background-color: var(--font-color);
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    border-bottom: 5px solid var(--nav-color);
    transition: all 0.1s;
  }
  
  .tech__icons {
    height: 70px;
    background-color: var(--light-color);
    border-radius: 50%;
  }
  
  .tech__card:hover {
    background-color: var(--font-color-hover);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
.portfolio {
    background-color: aqua;
  }
  
  .portfolio__main__container h1, 
  .portfolio__main__container p {
    text-align: center;
    margin-top: 3rem;
  }

   .portfolio__main__container{
    /* margin:1rem 0; */
     /* display:flex; */
    /* flex-direction: row;  */
    /* margin: 2rem 18rem; */
   
    /* justify-content:space-evenly; */
    padding-top: 5rem;
    align-items:center;
    position: absolute;
    flex-basis: 1;
    z-index: 1;
    width: 100%;
  }  
  h1{
    /* position: absolute; */
    /* z-index: 1; */
    color:white;
  
  }


  #portfolio__card__container {
    width: 40%;
    margin:1rem;
    padding: 10px;
    border: none;
    display:flex;
    justify-content:space-evenly;
    /* margin-bottom: 20px;
    margin-right: 20px; */
   
   
  }
  
  .portfolio__image:hover {
    cursor: pointer;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
  }
  
  .portfolio__click__info {
    position: absolute;
    top: 50%;  
    left: 50%; 
    -webkit-transform: translate(-50%, -50%); 
            transform: translate(-50%, -50%);
    color: var(--font-color);
    display: none;
  }
  
  .portfolio__image:hover + .portfolio__click__info {
    display: block;
  }
  
  .modal-backdrop.show {
    opacity: .1 !important;
  }
  
  #portfolio__modal__link {
    padding: 0 0 1rem 1rem
  }
  
  #portfolio__modal__link:link {
    text-decoration: none;
  }
 
  
  @media only screen and (max-width: 600px){
      .portfolio__main__container {
      width: 100%;
        /* padding: 3rem 3rem; 
      display:flex !important; */
      direction:column; 
      border: none;
      z-index: 1;
      position:absolute;
      /* overflow:absolute */
      
    }
  }
.contactMe__container {
  background-color: var(--nav-color);
  color: aliceblue;
  padding: 1rem;
  display: flex;
  flex-direction: column
  }
  
  .contactMe__data__links {
    display: flex;
    justify-content: space-evenly;
  }
  
  .contactMe__data__links a {
    color: white;
    text-decoration: none;
    text-transform: uppercase;

  }
  .link {
    margin:0 auto
  }
  
  .contactMe__data__links a:hover {
    color: var(--light-color);
  }
  
  @media only screen and (max-width: 600px){
    .contactMe__data__links {
      flex-direction: column;
    }
  }




